const Div = styled.div`
    margin-left: auto;
    margin-right: auto;
    width: 95%;
    margin-top: 20px;
    max-width: 330px;
`

const Bordered = styled.div`
    border: 1px solid white;
    border-radius: 10px;
    margin-top: 20px;
    padding-bottom: 20px;
    background-color: #43586b;
`
const Grid = styled.div`
    display: grid;
    flex-wrap: wrap;
    grid-template-columns: 1fr 1fr 1fr 1fr;
    margin: auto;
    margin-top: -10px;
    margin-bottom: 50px;
`
const Oppgave = styled.div`
    padding: 0px 20px 5px 20px;
    text-align: justify;
    margin-bottom: 10px;
`
const Lenke = styled.div`
    padding-bottom: 10px;
`
const Lenker = styled.div`
    margin-top: -30px;
`
const Item = styled.div`
    margin: 5px;
    width: 66px;
    height: 66px;
    line-height: 66px;
    background-color: grey;
    text-align: center;
    color: white;
    font-size: 36px;
    border-radius: 10px;
`

const Svar = styled.div`
    background-color: rgba(0,0,0,0.2);
    padding: 10px 0px;
    width: 80%;
    margin-left:auto;
    margin-right:auto;
`

interface HjemProps {
    setState: (input: State) => void
}

export const Hjem = ({ setState }: HjemProps)  => {
    const d = new Date();
    const klokkeslett = d.getHours()
    const dato = (d.getDate() > 24) ? 0 : d.getDate();
    const [oppgaveValgt, setOppgaveValgt] = useState<number>(0)
    const [visSvar, setVisSvar] = useState(false)

    return (
        <Div>
            {oppgaveValgt === 0 && (
                <div>
                    <Grid>
                        {data.map((element) => (
                            <Item
                                onClick={() => { if ((element.dag <= dato)) { setOppgaveValgt(element.dag) } }}
                                style={{
                                    backgroundColor: (element.dag === dato) ? "rgba(235, 221, 150, 0.1)" : ((element.dag < dato) ? "rgba(255, 255, 255, 0.03)" : "rgba(0, 0, 0, 0)"),
                                    color: ((element.dag === dato) ? "#ebdd96" : ((element.dag < dato) ? "white" :"black")),
                                    opacity: ((element.dag > dato) ? 0.2 : 1),
                                    border: ((element.dag === dato) ? "2px solid #ebdd96" : ((element.dag > dato) ? "2px solid black" : "2px solid rgba(158, 247, 161, 0.3)")),
                                    cursor:  ((element.dag <= dato) ? "pointer" : "default")
                                }}
                            >
                                {element.dag}
                            </Item>
                        ))}
                            </Grid>
                    <Lenker>        
                    {dato !== 1 && (
                        <Lenke>
                                <button onClick={ () => setState('TOPPLISTE') }><BsTrophyFill/> Sjekk topplisten <BsTrophyFill/></button>
                        </Lenke>
                    )}
                    <Lenke>
                        <a href="https://www.facebook.com/groups/131845715156441"><BsFacebook/> Gå til Facebook-siden <BsFacebook/></a>
                        </Lenke>
                        <p>Har du spørsmål eller innspill? <br/>  Kontakt oss på <a href="mailto:hei@julereisen.no">hei@julereisen.no</a></p>
                    </Lenker>        
                </div>
            )}
            {oppgaveValgt !== 0 && (
                <div style={{marginTop: "20px"}}>
                    <button onClick={() => {
                        setOppgaveValgt(0)
                        setVisSvar(false)
                    }
                    }><BsArrowLeft/> Gå tilbake til oppgavene</button>
                    <Bordered>
                        <h3>Oppgave for {oppgaveValgt}. Desember:</h3>
                        {((oppgaveValgt === dato && klokkeslett >= 9) || (oppgaveValgt < dato) )&& (
                            <Oppgave>{data.find((element) => element.dag === oppgaveValgt)?.beskrivelse}</Oppgave>
                        )}
                        {(oppgaveValgt === dato && klokkeslett < 9) && (
                            <Oppgave>Oppgaven kommer kl. 09:00</Oppgave>
                        )}
                        {oppgaveValgt === dato && (
                            <>
                                {klokkeslett >= 18 && (
                                    <Oppgave><i>Hint: {data.find((element) => element.dag === oppgaveValgt)?.hint}</i></Oppgave>
                                )}
                                {klokkeslett < 18 && (
                                    <Oppgave><i>Står du fast? Hint kommer kl. 18:00</i></Oppgave>
                                )}
                                <h3>Har du funnet løsningen?</h3>
                                <a href="https://forms.gle/z25GJuVRQbXFwzAu8">Send inn svaret ditt her</a>
                            </>
                        )}
                        {dato > oppgaveValgt && (
                            <Oppgave><i>Hint: {data.find((element) => element.dag === oppgaveValgt)?.hint}</i></Oppgave>
                        )}
                        {(oppgaveValgt < dato || (oppgaveValgt === dato+1 && klokkeslett >= 9)) && (
                            <Svar>
                                {oppgaveValgt < dato && (
                                    <div>
                                        <button onClick={() => setVisSvar(!visSvar)}>{ visSvar ? "Skul" : "Vis"} svar</button>
                                    </div>
                                )}
                                {oppgaveValgt < dato && visSvar && (
                                    <div>
                                        <h3>Svar:</h3>
                                        <p>{data.find((element) => element.dag === oppgaveValgt)?.svar}</p>
                                    </div>
                                )}
                            </Svar>
                        )}
                    </Bordered>
                </div>
            )}
        </Div>
    )
}